<script>
export default {
  name: 'RankingData',
  props: ['data'],
}
</script>

<template>
  <div class="box">
    <div class="song-info">
      <strong style="font-size:20px">#{{data.ranking}}  </strong>
      <span style="color:red" v-if="data.last_score===0">▲</span>
      <span style="color:red" v-else-if="data.last_score!==-1&&data.ranking-data.last_ranking<0">▲</span>
      <span style="color:darkgreen" v-else-if="data.last_score!==-1&&data.ranking-data.last_ranking>0">▼</span>
      <span v-else></span>
      <span style="color:red" v-if="data.last_score===-1">NEW!!</span>
      <span style="color:red" v-else-if="data.last_score===0">--</span>
      <span style="color:red" v-else-if="data.ranking-data.last_ranking<0">{{Math.abs(data.ranking-data.last_ranking)}}</span>
      <span style="color:darkgreen" v-else-if="data.ranking-data.last_ranking>0">{{Math.abs(data.ranking-data.last_ranking)}}</span>
      <span v-else>{{Math.abs(data.ranking-data.last_ranking)}}</span><br/>
      <div class="song-title-div">
        <span class="song-title">
          <strong style="font-size: 1.5em">{{data.song_title}}</strong>
          <small class="song-title-zh" v-show="data.song_title_zh!==''">({{data.song_title_zh}})</small>
        </span>
      </div>
      {{data.song_artist}} / {{data.song_vocalbank}}<br/>
      {{data.start_time}} {{data.content_id}}<br/>
      <div class="urls">
        <a class="btn btn-niconico" :href="`https://www.nicovideo.jp/watch/${data.content_id}`" target="_blank">Niconico</a>
        <a class="btn btn-bilibili" v-show="data.song_bili_id!==''" :href="`https://www.bilibili.com/video/${data.song_bili_id}`" target="_blank">Bilibili本家</a>
        <a class="btn btn-bilibili" v-show="data.song_bili_id===''&&data.song_bili_reprint_id!==''" :href="`https://www.bilibili.com/video/${data.song_bili_reprint_id}`" target="_blank">Bilibili搬运</a>
        <a class="btn btn-youtube" v-show="data.song_ytb_id!==''" :href="`https://www.youtube.com/watch?v=${data.song_ytb_id}`" target="_blank">Youtube</a>
      </div>
    </div>
    <div class="song-data">
      <div class="row row-song-data">
        <div class="col-sm-9">
          <div class="row">
            <div class="col-sm-3 col-6">
              <span class="weight"><strong>播放：</strong>{{data.weight_x.toFixed(2)}}×</span>
              <span class="score">{{data.view_delta.toLocaleString()}}</span>
            </div>
            <div class="col-sm-3 col-6">
              <span class="weight"><strong>评论：</strong>{{data.weight_a.toFixed(2)}}×</span>
              <span class="score">{{data.comment_delta.toLocaleString()}}</span>
            </div>
            <div class="col-sm-3 col-6">
              <span class="weight"><strong>收藏：</strong>{{data.weight_b.toFixed(2)}}×</span>
              <span class="score">{{data.mylist_delta.toLocaleString()}}</span>
            </div>
            <div class="col-sm-3 col-6">
              <span class="weight"><strong>点赞：</strong>{{data.weight_b.toFixed(2)}}×</span>
              <span class="score">{{data.like_delta.toLocaleString()}}</span>
            </div>
          </div>
        </div>
        <div class="col-sm-3">
          <span class="weight"><strong>TOTAL: </strong></span>
          <span class="score">{{data.total_score.toLocaleString()}}</span>
          <span class="weight"> PTS </span>
          <span class="weight" style="color:red" v-if="data.last_score===0">▲</span>
          <span class="weight" style="color:red" v-else-if="data.last_score!==-1&&data.total_score-data.last_score>0">▲</span>
          <span class="weight" style="color:darkgreen" v-else-if="data.last_score!==-1&&data.total_score-data.last_score<0">▼</span>
          <span class="weight" v-else></span>
          <span class="weight" style="color:red" v-if="data.last_score===-1">NEW!!</span>
          <span class="weight" style="color:red" v-else-if="data.last_score===0">--%</span>
          <span class="weight" style="color:red" v-else-if="(data.total_score/data.last_score-1)>0">{{Math.abs((data.total_score/data.last_score-1)*100).toFixed(2)+"%"}}</span>
          <span class="weight" style="color:darkgreen" v-else>{{Math.abs((data.total_score/data.last_score-1)*100).toFixed(2)+"%"}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped>
.box{
  border: 0.1em solid #000;
  border-radius: 1em;
  margin-top: 1em;
  margin-bottom: 1em
}
.song-info{
  margin-left: 1em;
  margin-right: 1em;
}
.song-title-div{
  overflow-x: auto;
  white-space: nowrap;
}
.song-title-zh{
  color:gray;
}
.song-data{
  background-color: lightsteelblue;
  border-radius: 0 0 1em 1em
}
.row-song-data{
  margin-left: 0.3em;
  margin-right: 0.3em;
}
.weight{
  font-size:1em;
}
.score{
  font-size: 1.2em;
  font-weight: bold;
}
.urls{
  margin-bottom: 0.5em;
}
.btn{
  margin-right: 0.3em;
}
.btn-niconico{
  background-color: #222222;
  border-color: black;
  color: white;
}
.btn-niconico:hover{
  background-color: #000000
}
.btn-bilibili{
  background-color:skyblue;
  border-color: lightblue;
  color: white;
}
.btn-bilibili:hover{
  background-color:deepskyblue;
}
.btn-youtube{
  background-color:whitesmoke;
  border-color: red;
  color: red;
}
.btn-youtube:hover{
  background-color:white;
}
</style>